import { ref } from "vue";
import LoginAccount from "./LoginAccount.vue"; // import LoginPhone from "./LoginPhone.vue"

import { useStore } from "vuex";
export default {
  components: {
    // LoginPhone,
    LoginAccount
  },

  setup() {
    const store = useStore(); // 是否記住密碼

    let isRemember = ref(store.getters.account.isRemember); // Ref用來傳遞組件間訊息

    const accountRef = ref();
    const phoneRef = ref();
    const elTabRef = ref("account");

    const loginHandler = () => {
      if (elTabRef.value === "account") {
        accountRef.value?.handleLogin(isRemember.value);
      } else {
        phoneRef.value?.handleLogin();
      }
    };

    return {
      isRemember,
      loginHandler,
      accountRef,
      elTabRef,
      phoneRef
    };
  }

};