import { reactive, ref } from "vue"; // 表單驗證規則

import { rules } from "../config/accountConfig";
import { useStore } from "vuex";
export default {
  setup() {
    const store = useStore(); // 帳密

    const account = reactive({
      account: store.getters.account.account ?? "",
      password: store.getters.account.password ?? ""
    });
    const formRef = ref();

    const handleLogin = isRemember => {
      // 表單驗證
      formRef.value?.validate(valid => {
        if (valid) {
          // 緩存帳號密碼
          if (isRemember === true) {
            store.dispatch("login/setAccount", { ...account,
              isRemember
            });
          } else {
            store.dispatch("login/resetAccount");
          } // 登入


          store.dispatch("user/loginAction", { ...account
          });
        }
      });
    };

    return {
      formRef,
      rules,
      account,
      handleLogin
    };
  }

};